import React, { Component } from 'react';

class Nav extends Component {
  menu = ['intro', 'about', 'briefcase', 'contact']
  constructor(props) {
    super(props)
    this.state ={ }
  }

  handleMenu(e) {
    if (e.target.hash === '#intro') {
      window.scrollTo(0,0)
    }
    Array.from(document.getElementsByClassName('c-nav__menu-item')).forEach(menuItem => {
      menuItem.parentElement.classList.remove('is-intoview')
    })
    e.target.parentElement.classList.add('is-intoview')
  }

  handleScroll(menu) {
    var values = []
    this.menu.forEach(section => {
      if (document.querySelector(`.c-${section}`).getBoundingClientRect().bottom >= 0) {
        values.push(document.querySelector(`.c-${section}`).getBoundingClientRect().bottom)
      }
    })
    var minValue = Math.min(...values)
    this.menu.forEach(section => {
      if (document.querySelector(`.c-${section}`).getBoundingClientRect().bottom >= 0 && document.querySelector(`.c-${section}`).getBoundingClientRect().bottom <= minValue) {
        Array.from(document.getElementsByClassName('c-nav__menu-item')).forEach(menuItem => {
          menuItem.parentElement.classList.remove('is-intoview')
        })
        document.querySelector(`.${section}`).parentElement.classList.add('is-intoview')
      }
    })
  }

  render() {
    return (
      <div className="c-nav">
        <div className="c-nav__logo u-bg-contain-ctr" alt="logo"></div>
        <div className="c-nav__menu">
          <ul>
            {this.menu.map((item, index) =>
              <li className={index === 0 ? 'c-nav__menu-list is-intoview' : 'c-nav__menu-list' } key={index}>
                <a onClick={(e) => this.handleMenu(e)} className={ `c-nav__menu-item c-heading c-heading--link c-heading--italic ${item}` } href={`#${item}`}>{item}</a>
              </li>
            )}
          </ul>
        </div>
      </div>
    );
  }

  componentDidMount() {
    window.addEventListener('scroll', () => this.handleScroll(this.menu))
  }
}

export default Nav;
